



















import { Vue, Component } from "vue-property-decorator";
import TodayChallengeList from "@/components/TodayChallengeList.vue";
import Timeline from "@/components/Timeline.vue";
import CommonModule from "@/store/modules/CommonModule";

/**
 * 홈
 */
@Component({
  components: {
    TodayChallengeList,
    Timeline
  }
})
export default class Home extends Vue {
  get selectChallenge() {
    return CommonModule.instance.selectedChallenge;
  }

  get isPreview() {
    return CommonModule.instance.isPreview;
  }

  joinChallenge(title: string) {
    console.log("joinChallenge", title);
    CommonModule.instance.setSelectedChallenge(title);
    CommonModule.instance.setIsPreview(false);
  }

  previewChallenge(title: string) {
    console.log("previewChallenge", title);
    CommonModule.instance.setSelectedChallenge(title);
    CommonModule.instance.setIsPreview(true);
  }
}
