



























import { Vue, Component, Prop } from "vue-property-decorator";
import { CommonModule } from "@/modules";

/**
 * 오늘의 챌린지 아이템
 */
@Component
export default class TodayChallengeItem extends Vue {
  @Prop() id!: number;
  @Prop() title!: string;
  @Prop() totalCount!: number;
  @Prop() successCount!: number;

  get isLogin() {
    return CommonModule.instance.isLogin;
  }

  onClickJoinBtn() {
    if (CommonModule.instance.isLogin) {
      this.$emit("join-challenge", this.title);
    }
  }

  onClickPreviewBtn() {
    this.$emit("preview-challenge", this.title);
  }
}
