
























import { Vue, Component } from "vue-property-decorator";
import TodayChallengeItem from "@/components/TodayChallengeItem.vue";

/**
 * 오늘의 챌린지 리스트
 */
@Component({
  components: {
    TodayChallengeItem
  }
})
export default class TodayChallengeList extends Vue {
  get challengeList() {
    return ["그림 그리기", "라디오 듣기", "일기 쓰기", "요리 하기"];
  }

  joinChallenge(title: string) {
    this.$emit("join-challenge", title);
  }

  previewChallenge(title: string) {
    this.$emit("preview-challenge", title);
  }
}
