



























































































import { Vue, Component, Prop } from "vue-property-decorator";

/**
 * 타임라인 아이템
 */
@Component
export default class TimelineItem extends Vue {
  @Prop() id!: number;
  @Prop() isPreview!: boolean;

  imageUrl = `https://picsum.photos/500?random=${Math.ceil(
    Math.random() * 100
  )}`;
  nickname = "";
  content = "";

  // 좋아요
  isGood = false;
  isGoodCount = Math.ceil(Math.random() * 100);

  // 코멘트
  commentList: {
    nickname: string;
    comment: string;
  }[] = [];
  isCommentOpen = false;
  newComment = "";
  collapseId = `collapse-${this.id}`;

  onClickGood() {
    console.log("onClickGood");
    if (!this.isPreview) {
      this.isGood = !this.isGood;
      if (this.isGood) {
        this.isGoodCount += 1;
      } else {
        this.isGoodCount -= 1;
      }
    }
  }

  onClickShare() {
    console.log("onClickShare");
  }

  onClickMoreBtn() {
    console.log("onClickMoreBtn");
    this.isCommentOpen = true;
  }

  onClickNewComment() {
    const newComment = this.newComment.trim();
    if (newComment) {
      this.commentList.push({
        nickname: "me",
        comment: newComment
      });

      if (!this.isCommentOpen) {
        this.isCommentOpen = true;
      }
    }

    this.newComment = "";
  }

  mounted() {
    const nicknameList = [
      "scanties",
      "conclusory",
      "diestrus",
      "ataxic",
      "saccharometers",
      "oddish",
      "fastener",
      "fisherfolk",
      "pooftahs",
      "ruthful"
    ];

    this.nickname =
      nicknameList[Math.floor(Math.random() * nicknameList.length)];

    const contentList = [
      "She nervously peered over the edge. She understood in her mind that the view was supposed to be beautiful, but all she felt was fear. There had always been something about heights that disturbed her, and now she could feel the full force of this unease. She reluctantly crept a little closer with the encouragement of her friends as the fear continued to build. She couldn't help but feel that something horrible was about to happen.",
      "There wasn't a bird in the sky, but that was not what caught her attention. It was the clouds. The deep green that isn't the color of clouds, but came with these. She knew what was coming and she hoped she was prepared.",
      "I'm going to hire professional help tomorrow. I can't handle this anymore. She fell over the coffee table and now there is blood in her catheter. This is much more than I ever signed up to do.",
      "Since they are still preserved in the rocks for us to see, they must have been formed quite recently, that is, geologically speaking. What can explain these striations and their common orientation? ",
      "He picked up the burnt end of the branch and made a mark on the stone. Day 52 if the marks on the stone were accurate. He couldn't be sure. Day and nights had begun to blend together creating confusion, but he knew it was a long time. Much too long.",
      "What was beyond the bend in the stream was unknown. Both were curious, but only one was brave enough to want to explore. That was the problem. There was always one that let fear rule her life.",
      "It was difficult to explain to them how the diagnosis of certain death had actually given him life. While everyone around him was in tears and upset, he actually felt more at ease.",
      "She wondered if the note had reached him. She scolded herself for not handing it to him in person. She trusted her friend, but so much could happen. She waited impatiently for word.",
      "She had been told time and time again that the most important steps were the first and the last.",
      "He heard the crack echo in the late afternoon about a mile away. ",
      "He watched as the young man tried to impress everyone in the room with his intelligence. ",
      "A car was coming down the street and with her arm outstretched and thumb in the air, she had a plan.",
      "he continued, laying the trap he'd been planning for years.",
      "Sleeping in his car was never the plan but sometimes things don't work out as planned."
    ];

    this.content = contentList[Math.floor(Math.random() * contentList.length)];

    const commentList = [
      "It was getting dark, and we weren’t there yet.",
      "A purple pig and a green donkey flew a kite in the middle of the night and ended up sunburnt.",
      "She only paints with bold colors; she does not like pastels.",
      "He shaved the peach to prove a point.",
      "Americans use forks instead of chopsticks.",
      "The blue parrot drove by the hitchhiking mongoose.",
      "The bullet pierced the window shattering it before missing Danny's head by mere millimeters.",
      "We're careful about orange ping pong balls because people might think they're fruit.",
      "I don't like driving in the city.",
      "The hummingbird's wings blurred while it eagerly sipped the sugar water from the feeder."
    ];

    for (let i = 0; i < Math.floor(Math.random() * 10); ++i) {
      this.commentList.push({
        nickname: nicknameList[Math.floor(Math.random() * nicknameList.length)],
        comment: commentList[Math.floor(Math.random() * commentList.length)]
      });
    }
  }
}
